@mixin user_select_none {
  -moz-user-select: none;
  -khtml-user-select: none;
  user-select: none;
}
@mixin auth_title_style {
  margin: 0px 0px 15px 0px;

  font-size: 26px;
  font-weight: 600;
}
@mixin table_body_styles {
  .imageWrapper {
    position: absolute;
    top: 0;
    left: 0;

    display: flex;
    align-items: center;

    height: 100%;
    padding: 0 10px;

    transition: 0.2s ease transform 0.2s;
    @media (max-width: 599px) {
      z-index: 1;

      width: 100%;
      padding: unset;
    }
  }

  .itemName {
    transform: rotateX(-90deg);

    transition: 0.2s ease transform 0s;
  }

  .itemNameAdaptive {
    z-index: 2;
  }

  .itemImage {
    width: 80px;
    height: 64px;

    object-fit: cover;
    border: 1px solid rgb(128 128 128 / 67.2%);
    @media (max-width: 599px) {
      width: 100%;
      height: 100%;

      opacity: 0.25;
      border: none;
    }
  }

  .tableItem__body {
    @media (max-width: 599px) {
      display: block;

      width: 100%;

      font-weight: 600;
    }
  }

  .tableItem__bodyRow {
    height: 70px;

    background: white;

    &:not(:last-child) {
      border-bottom: 2px solid #efefef;
    }

    &:hover {
      background: #f5f5f5;
    }
    @media (max-width: 599px) {
      position: relative;

      overflow: hidden;
      display: block;

      width: 100%;
      height: 100%;

      border: 1px solid rgb(189 188 188);
      border-radius: 15px;

      &:not(:last-child) {
        margin: 0px 0px 15px 0px;

        border-bottom: 1px solid rgb(189 188 188);
      }
    }
  }
}
